.card .activities {
    text-align: left;

    .activity {
        position: relative;
        padding: 1px .5rem 1px 1.5rem;
        &::before {
            content: "";
            position: absolute;
            width: .8rem;
            height: 1.7rem;
            left: 0;
            top: 0;
            color: $color-pink;
            background-color: $color-pink;
            clip-path: polygon(0% 0%, 100% 50%, 0% 100%);
        }
        

        &:not(:last-child) {
            border-bottom: 1px solid $color-black;
        }

        .comment { font-style: italic; }

        & * {
            font-size: 1.1rem;

        }
    }
}