header {
    display: grid;
    justify-items: right;
    align-items: center;
    grid-template-areas: 
        'logo menu lang'
        'logo search login';
    grid-template-rows: 35% 65%;
    background: -webkit-linear-gradient($color-pink, $color-pink 3%, $color-white 3%, $color-white 35%, $color-black 35%);
    max-width: 100vw;
    padding: 2px calc((100vw - 98rem) / 2) 0 calc((100vw - 98rem) / 2);
    height: 8.5rem;

    & > a:first-child {
        grid-area: logo;
        justify-self: left;
        line-height: 0;
        img.logo {
            max-height: 7rem;
            max-width: 100vw;
        }
    }


    ul {
        display: flex;
        list-style: none;

        &.menu {
            grid-area: menu;
            li:not(:last-child) {
                margin-right: 1rem;
            }

            a:link, a:visited {
                color: black;
                text-decoration: none;
                font-size: 1.6rem;
                font-weight: bold;
                text-transform: uppercase;

                &:hover, &:active, &:focus { 
                    color: $color-pink; 
                }
            }
        }

        &.lang {
            grid-area: lang;
            li:not(:last-child) {
                margin-right: .5rem;
            }
            
            a:link, a:visited {
                color: black;
                text-decoration: none;
                font-size: 1.3rem;
                text-transform: uppercase;

                &:hover, &:active, &:focus { 
                    border-bottom: 1px solid $color-pink; 
                }
            }
        }
    }

    form#search {
        grid-area: search;
        display: flex;
        width: 100%;
        font-size: 1.4rem;
        input[type="text"] {
            flex: 1 1 20rem;
            padding: .4rem;
            border: solid 1px $color-brown;
            color: $color-grey;
        }
        input[type="submit"] {
            padding: 0 .8rem;
            color: $color-white;
            background: $color-pink;
            border: none;
            font-weight: bold;
            cursor: pointer;
            &[disabled] {
                cursor: not-allowed;
                filter: brightness(.9);
            }
            &:not([disabled]):hover { color: $color-black; }
        }
    }

    .login {
        display: flex;

        a {
            color: white;
            text-decoration: none;
            font-size: 1.1rem;

            &:hover, &:active, &:focus {
                color: $color-pink;
            }
        }

        span {
            margin: 0 .5rem; 
            color: $color-white;        
        }

        form#login {
            grid-area: login;
            max-width: 10rem;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
    
            > * { min-width: 0; }
    
            input[type="text"], input[type="password"] {
                border: solid 1px $color-brown;
                color: $color-grey;
                padding: .1rem;
    
                &:first-child {
                    margin-bottom: .5rem;
                }
            }
    
            input[type="password"] { flex: 0 1 78%; }
            input[type="submit"] { 
                flex: 0 0 20%;
                font-size: 1rem;
                color: black;
                background: white;
                border: solid 1px $color-brown;
                &[disabled] {
                    cursor: not-allowed;
                    filter: brightness(.9);
                }
            }
        }
    }


    @include respond($tab-land) {
        ul, form#search, .login {
            display: none;
        }
        & {
            display: flex;
            justify-content: center;
        }
    }

    @include respond($phone) {
        height: fit-content;
        padding: .5rem;
    }
}