// Animasher.com base styles:
abbr,acronym{border-bottom:1px dotted #000;cursor:help}em{font-style:italic}blockquote{margin:1em}dl dd{margin-left:1em}td,th{border:1px solid #000;padding:.5em}th{font-weight:700;text-align:center}caption{margin-bottom:.5em;text-align:center}fieldset,pre,table{margin-bottom:1em}
//

body { font-family: arial, helvetica, sans-serif; font-size: 1.3rem; }

h1 { font-size: 1.8rem; margin-bottom: 1rem;}

h2 { font-size: 1.6rem; }

h3 { font-size: 1.4rem; }

h1,h2,h3,h4,h5,h6,strong { font-weight:bold; }

a, a:visited { text-decoration: none; color: $color-pink; }

a, p { font-size: 1.3rem; }

.pink { color: $color-pink; }

h3.pinkheader {
    color: $color-pink;
    font-size: 1.5rem;
    margin-bottom: .8rem;
}